import { Box } from "@rp/react/web/mui/core"
import { CloudOffOutlined, CloudUploadOutlined } from "@rp/react/web/mui/icons"

type Props = {
  disabled?: boolean
  size?: number | string
}

export const UploadBoxIcon = ({ disabled = false, size = "50px" }: Props) => {
  return (
    <Box
      sx={{
        width: size,
        height: size,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        color: "primary.main",
        backgroundColor: "primary.background",
        mb: 1,
        borderRadius: 1,
      }}
    >
      {disabled ? <CloudOffOutlined /> : <CloudUploadOutlined />}
    </Box>
  )
}
